import request from '@yy/sl-theme-shared/utils/request';
import { t } from '@yy/sl-theme-shared/utils/i18n';
import Base from '../../shared/base/BaseClass';
import checkEmail from '../../shared/utils/checkEmail';
import debounce from '../../shared/utils/debounce';
import Toast from '../../shared/components/toast';

class Newsletter extends Base {
  config = {
    namespace: 'satge:sign-up-and-save',
  };

  constructor() {
    super();
    this.$setNamespace(this.config.namespace);
    this.init();
  }

  init() {
    this.toast = new Toast({
      duration: 2000,
      fullscreen: true,
    });
    this.bindSubscription();
  }

  bindSubscription() {
    const post = debounce(300, (val) => {
      const params = {
        subscribeAccountType: 'email',
        subscribeAccount: val,
      };
      const referralCode = window?.SLMemberPlugin?.memberReferralCode?.value || null;
      if (referralCode) {
        params.referralCode = referralCode;
      }

      request
        .post('/user/front/users/footersub', params)
        .then((res) => {
          if (res.success) {
            this.toast.open(t('footer.subscribeSuccess'), 2000);
          } else {
            this.toast.open(t('footer.subscribeRegExpError'), 2000);
          }
        })
        .catch((err) => {
          console.log('subscribe error', err);
          this.toast.open(t('footer.subscribeRegExpError'), 2000);
        });
    });

    this.$on('click', '.newsletter__btn', (e) => {
      const $input = $(e.currentTarget).siblings('input');
      const value = $input.val();
      if (checkEmail(value) !== true) {
        this.toast.open(t('footer.subscribeRegExpError'), 2000);
        return;
      }
      post(value);
    });
  }
}

export default new Newsletter();
