import Player from './Player';
import LibraryLoader from './LibraryLoader';

let vimeoReady = false;

const defaults = {
  byline: false,
  controls: true,
  loop: false,
  muted: true,
  playsinline: true,
  portrait: false,
  responsive: true,
  transparent: false,
  title: 'vedio',
  autoplay: false,
};
export default class VimeoPlayer extends Player {
  constructor(containerId, options) {
    super();
    this.el = $(containerId).get(0);

    this.options = {
      ...defaults,
      ...options,
      id: options.videoId,
    };
    if (vimeoReady) {
      this.init();
    } else {
      LibraryLoader.load('vimeo', window.vimeoApiReady);
      window.SL_EventBus.on('stage:vimeoReady', this.init.bind(this));
    }
  }

  init() {
    vimeoReady = true;
    console.log(this.options);
    this.player = new window.Vimeo.Player(this.el, this.options);
    this.player.ready().then(this.playerReady.bind(this));
  }

  playVideo() {
    this.player.play();
  }

  mute() {
    this.player.setMuted(true);
  }

  unMute() {
    this.player.setMuted(false);
  }

  destroy() {
    this.player.destroy();
  }

  playerReady() {
    this.options.events.onReady();
  }
}
