/* eslint-disable camelcase */
import Player from './Player';
import LibraryLoader from './LibraryLoader';

let youTubeReady = false;
window.onYouTubeIframeAPIReady = function () {
  window.SL_EventBus.emit('stage:youTubeReady');
  youTubeReady = true;
  // window.SL_EventBus.emit('stage:youTubeReady:');
};
const videoOptions = {
  ratio: 16 / 9,
  scrollAnimationDuration: 400,
  playerVars: {
    autohide: 0,
    autoplay: undefined,
    cc_load_policy: 0,
    controls: 1,
    fs: 0,
    iv_load_policy: 3,
    modestbranding: 1,
    playsinline: 1,
    rel: 0,
  },
  events: {
    onReady: (event) => {
      console.log(event);
    },
    onStateChange: (event) => {
      console.log('change', event);
    },
    onError: (event) => {
      console.log('onError', event);
    },
  },
};

export default class YouTube extends Player {
  constructor(containerId, options = {}) {
    super();

    this.containerId = containerId;
    this.options = { ...videoOptions, ...options };

    if (youTubeReady) {
      this.init();
    } else {
      LibraryLoader.load('youtubeSdk');
      window.SL_EventBus.on('stage:youTubeReady', this.init.bind(this));
    }
  }

  init() {
    youTubeReady = true;
    this.player = new window.YT.Player(this.containerId, this.options);
  }

  playVideo() {
    this.player?.playVideo();
  }

  mute() {
    this.player?.mute();
  }

  unMute() {
    this.player?.unMute();
  }

  destroy() {
    this.player?.destroy();
  }
}
