import Base from '../../shared/base/BaseClass';

class Faq extends Base {
  config = {
    namespace: 'stage:faq',
  };

  constructor() {
    super();
    this.$setNamespace(this.config.namespace);
    this.init();
  }

  init() {
    this.bindBlockCollapse();
  }

  bindBlockCollapse() {
    this.$on('click', '.faq__collapse-title', (e) => {
      let $target = $(e.target);
      if (e.target.classList.value.indexOf('faq__collapse-icon') > -1) {
        $target = $target.parent();
      }
      const $wrapper = $target.next();
      const $content = $wrapper.find('.faq__collapse-content');

      if ($wrapper.hasClass('active')) {
        $target.removeClass('active');
        $wrapper.removeClass('active');
        $wrapper.height(0);
      } else {
        $wrapper.addClass('active');
        $target.addClass('active');
        $wrapper.height($content.outerHeight(true));
      }
    });
  }
}

export default new Faq();
