/* eslint-disable max-classes-per-file */
import Swiper, { Navigation, Pagination, Autoplay, Keyboard } from 'swiper';

import EffectFlickityFade from './swiper/effect-flickity-fade';

import 'swiper/components/effect-fade/effect-fade.min.css';

Swiper.use([EffectFlickityFade, Navigation, Pagination, Autoplay, Keyboard]);

const SLIDE_ACTIVE_CLASS = 'is-selected';

const selectors = {
  flickityViewport: '.flickity-viewport',
  image: '.hero__image',
  imageWrapper: '.hero__image-wrapper',
  imageMobile: '.hero__image--mobile',
  slide: '.slideshow__slide',
};

class Slideshow {
  container = null;

  settings = {};

  sectionId = '';

  constructor(container) {
    this.container = container;

    this.sectionId = container.data('section-id');

    try {
      this.settings = JSON.parse($(`#Slideshow-data-${this.sectionId}`).text());
    } catch (err) {
      //
    }
    this.init();
  }

  init() {
    const { container, sectionId, settings } = this;
    const slides = container.find(selectors.slide);

    const swiperOptions = {
      grabCursor: true,
      enabled: slides.length > 1,
      speed: 0,
      effect: 'flickity-fade',
      slideActiveClass: SLIDE_ACTIVE_CLASS,
      wrapperClass: 'flickity-slider',
      loop: true,
      createElements: true,
      virtualTranslate: true,
      delay: 2000,
      keyboard: {
        enabled: true,
      },

      on: {
        fadeTransitionend() {
          // swiper.slides.removeClass(classes.parallaxContainer);
          // swiper.slides.find(selectors.imageWrapper).removeClass(classes.parallax);
          // swiper.slides.eq(this.activeIndex).addClass(classes.parallaxContainer).find(selectors.imageWrapper).addClass(classes.parallax);
          // window.SL_EventBus.emit('parallax');
        },
        slideChangeTransitionStart() {
          this.$el.removeClass('hero--static');
        },
        init() {
          window.lozadObserver.observe();
          window.SL_EventBus.emit('parallax');
        },
      },
    };
    if (settings.autoplay) {
      Object.assign(swiperOptions, {
        autoplay: {
          disableOnInteraction: false,
          delay: settings.autoplay_speed * 1000,
        },
      });
    }
    switch (settings.style) {
      case 'arrows':
        Object.assign(swiperOptions, {
          navigation: {
            nextEl: '.flickity-button--next',
            prevEl: '.flickity-button--prev',
          },
        });
        break;
      case 'dots':
      case 'bars':
        Object.assign(swiperOptions, {
          pagination: {
            el: '.swiper-pagination',
            clickable: true,
          },
        });
        break;
      default:
        break;
    }
    this.swiper = new Swiper(`#Slideshow-${sectionId}`, swiperOptions);
  }

  destroy() {
    console.info('swiper:destroy');
    this.swiper.destroy();
  }
}

export default class SlideshowSection {
  static type = 'slideshow';

  slideshow = null;

  constructor(container) {
    if (!container.find(selectors.slide).length) return;

    this.slideshow = new Slideshow(container);
  }

  onUnload() {
    if (this.slideshow) {
      this.slideshow.destroy();
    }
  }
}

