import initPreview from '../../product/detail/js/product-preview';

const sectionsMap = {};
const initFeatureProduct = ({ id, statePath }) => {
  sectionsMap[id] = initPreview({ id, statePath, filterShelves: true, offsetTop: 20 });
};
$('.stage__featured__product--wrapped').each((index, element) => {
  const sectionId = $(element).data('section-id');
  const statePath = $(element).data('state-path');
  const productId = $(element).data('product-id');
  if (sectionId && productId) {
    initFeatureProduct({ id: sectionId, statePath });
  }
});

const registerFeatureProduct = (e) => {
  const { sectionId: updateSectionId } = e.detail;
  const statePath = $(`#stage__featured__product--${updateSectionId}`).data('state-path');
  const productId = $(`#stage__featured__product--${updateSectionId}`).data('product-id');
  if (updateSectionId && productId) initFeatureProduct({ id: updateSectionId, statePath });
};
$(document).on('shopline:section:load', registerFeatureProduct);
$(document).on('shopline:section:unload', (e) => {
  const { sectionId } = e.detail;
  const unloadFn = sectionsMap[sectionId];
  if (unloadFn && typeof unloadFn === 'function') {
    unloadFn?.destroy?.();
  }
});
