import Slideshow from './slideshow';
import SplitSlideshow from './split-slideshow';
import Video from './video';

const instaceMap = new Map();
const constructorMap = new Map();
class Sections {
  constructor() {
    window.document.addEventListener('shopline:section:load', this._onSectionLoad.bind(this));
    window.document.addEventListener('shopline:section:unload', this._onSectionUnload.bind(this));
  }

  loads(constructors) {
    const _this = this;
    constructors.forEach((constructor) => {
      if (typeof constructor === 'undefined' || !constructor.type) return;

      constructorMap.set(constructor.type, constructor);
      const $sections = $(`[data-section-type='${constructor.type}']`);

      $sections.each(function () {
        const $container = $(this);

        _this._createInstace($container, constructor);
      });
    });
  }

  _createInstace(container, _constructor) {
    const id = container.data('section-id');
    if (!id) return;
    const constructor = _constructor || constructorMap.get(container.data('section-type'));
    if (typeof constructor !== 'function') return;
    instaceMap.set(id, new constructor(container));
  }

  _onSectionLoad(e) {
    const { sectionId } = e.detail;
    if (instaceMap.has(sectionId)) {
      const instace = instaceMap.get(sectionId);
      if (typeof instace.onUnload === 'function') {
        instace.onUnload.call(instace);
      }
    }
    const $container = $(`[data-section-id='${sectionId}']`);

    this._createInstace($container);
    console.log('_onSectionLoad', e);
  }

  _onSectionUnload(e) {
    const { sectionId } = e.detail;
    if (instaceMap.has(sectionId)) {
      const instace = instaceMap.get(sectionId);
      if (typeof instace.onUnload === 'function') {
        instace.onUnload.call(instace);
        instaceMap.delete(sectionId);
      }
    }

    console.log('_onSectionUnload', e);
  }
}
$(() => {
  const sections = new Sections();
  sections.loads([Slideshow, SplitSlideshow, Video]);
});
