import YouTube from './YouTube';
import VimeoPlayer from './VimeoPlayer';
import initWhenVisible from '../init-when-visible';

// for test: https://www.youtube.com/watch?v=V7BEzkRBp_g
// const hostYoutube = 'www.youtube.com';

// // for test: https://vimeo.com/451552454
// const hostVimeo = 'vimeo.com';
window.vimeoApiReady = function () {
  window.SL_EventBus.emit('stage:vimeoReady');
};

const selectors = {
  playVideoBtn: '.video-overlay__button',
  videoData: '.video-data',
};
const classes = {
  playing: 'video--playing',
};

export default class Video {
  static type = 'video';

  container = null;

  sectionId = '';

  config = {
    id: '',
  };

  settings = {};

  constructor(container) {
    this.container = container;
    this.sectionId = container.data('id');
    try {
      this.settings = JSON.parse(container.find(`#Video-data-${this.sectionId}`).text());
    } catch (err) {
      //
    }

    this.initEvent();

    initWhenVisible({
      element: container,
      callback: this.init.bind(this),
      threshold: 500,
    });
  }

  init() {
    const { container: video } = this;
    const { settings } = this;
    this.config = {
      id: video.attr('id'),
      videoKey: `Video-${video.data('id')}`,
      videoId: video.data('id'),
    };
    const dataDiv = this.container.find(selectors.videoData);
    const type = dataDiv.data('type');
    const videoId = dataDiv.data('video-id');

    const aspectRatios = dataDiv.data('aspect-ratio')?.split(':') || [16, 9];

    const events = {
      onReady: this.onVideoPlayerReady.bind(this),
    };
    switch (type) {
      case 'youtube':
        this.initYoutubeVideo(dataDiv.attr('id'), {
          videoId,
          ratio: aspectRatios[0] / aspectRatios[1],
          autoplay: settings.autoplay ? 1 : 0,
          muted: !!settings.quiet,
          events,
        });
        break;
      case 'vimeo':
        this.initVimeoVideo(dataDiv.attr('id'), {
          videoId,
          ratio: aspectRatios[0] / aspectRatios[1],
          autoplay: !!settings.autoplay,
          muted: !!settings.quiet,
          events,
        });
        break;
      default:
    }
  }

  initYoutubeVideo(videoId, options) {
    this.player = new YouTube(videoId, options);
  }

  initVimeoVideo(videoId, options) {
    this.player = new VimeoPlayer(`#${videoId}`, options);
  }

  initEvent() {
    this.container.find(selectors.playVideoBtn).on('click', () => {
      this.startVideoOnClick();
    });
  }

  onVideoPlayerReady() {
    // 
    if (this.settings.autoplay) {
      this.player.playVideo();
      if (this.settings.quiet) {
        this.player.mute();
      } else {
        this.player.unMute();
        this.player.playVideo();
      }
    }
  }

  initAutoplay() {
    console.log('22');
  }

  startVideoOnClick() {
    this.container.addClass(classes.playing);
    const { player } = this;

    player.playVideo();
  }

  onUnload() {
    console.log('video:unload', this);
    this.player?.destroy?.();
  }
}
