/* eslint-disable max-classes-per-file */
import Swiper, { Navigation, Pagination, Autoplay, Keyboard } from 'swiper';

import EffectFlickityFade from './swiper/effect-flickity-fade';

import 'swiper/components/effect-fade/effect-fade.min.css';

Swiper.use([EffectFlickityFade, Navigation, Pagination, Autoplay, Keyboard]);

const selectors = {
  flickityViewport: '.flickity-viewport',
  image: '.hero__image',
  imageWrapper: '.hero__image-wrapper',
  imageMobile: '.hero__image--mobile',
  slide: '.split-slideshow__slider',
  slideWrapper: '.split-slideshow__swiper',
  slideContainer: '.split-slideshow__wrapper',
};

const paginationConfigMap = {
  arrows: {
    navigation: {
      nextEl: '.pagination__btn--next',
      prevEl: '.pagination__btn--prev',
    },
  },
};

class SplitSlideshow {
  container = null;

  settings = {};

  sectionId = '';

  constructor(container) {
    this.container = container;
    this.sectionId = container.data('section-id');

    try {
      this.settings = JSON.parse($(`#split-slideshow-data-${this.sectionId}`).text());
    } catch (err) {
      //
    }
    this.init();
  }

  init() {
    const { container, settings } = this;

    const swiperOptions = {
      grabCursor: true,
      enabled: container.find(selectors.slide).length > 1,
      speed: 0,
      effect: 'flickity-fade',
      slideActiveClass: 'is-selected',
      wrapperClass: selectors.slideWrapper.slice(1),
      slideClass: selectors.slide.slice(1),
      loop: true,
      createElements: true,
      virtualTranslate: true,
      delay: 2000,
      keyboard: {
        enabled: true,
      },

      on: {
        init() {
          window.lozadObserver.observe();
          window.SL_EventBus.emit('parallax');
        },
        slideChangeTransitionStart() {
          this.$el.removeClass(`${selectors.slideContainer}--static`.replace('.', ''));
        },
      },
    };

    if (settings.autoplay) {
      Object.assign(swiperOptions, {
        autoplay: {
          disableOnInteraction: false,
          delay: settings.autoplay_speed * 1000,
        },
      });
    }

    if (settings.style) {
      Object.assign(
        swiperOptions,
        paginationConfigMap[settings.style] || {
          pagination: {
            el: '.split-slideshow__pagination',
            clickable: true,
          },
        },
      );
    }

    this.swiper = new Swiper(container.find(selectors.slideContainer)[0], swiperOptions);
  }

  destroy() {
    this.swiper.destroy();
  }
}

export default class SplitSlideshowSection {
  static type = 'split-slideshow';

  splitSlideshow = null;

  constructor(container) {
    if (!container.find(selectors.slide).length) return;
    this.splitSlideshow = new SplitSlideshow(container);
  }

  onUnload() {
    if (this.splitSlideshow) {
      this.splitSlideshow.destroy();
    }
  }
}
